  import{vNav} from "@/components"
  import Kinlink from "./kinlink";
  //  import send from "./caozuo";
  let canvas;
  const socket = io("http://123.56.29.65:8003", {'transports': ['websocket', 'polling']}); //
  socket.on('user joined', function (data) {
    console.log(data);

  });
  socket.on('user left', function (data) {
    console.log(data);

  });

  socket.on('privateReceive', function (data) {
    console.log("=================");
    console.log(data);
    switch (data.messageType) {
      case 14:
        play();
        break;
    }

  });

  socket.on('new message', function (data) {
    console.log(data);
    let jsonData = JSON.parse(data);
    console.log(jsonData)
    switch (jsonData.messageType) {
      case 9:
        console.log("")
        break;
      case 10:
        console.log("")
        break;
    }
    // $("#container").append("<h5>" + jsonData.messageType + jsonData.messageContent + "</h5>");

  });

  const sendCaozuo = function (data, fund) {
    if (data.messageType === 13) play = fund;
    socket.emit('privateSend', {
      uid: data.uid,
      remoteUid: "10000",
      playerUid: data.playerUid,
      playerUserName: data.playerUserName,
      playerAvatar: data.playerAvatar,
      matchID: data.matchID,
      messageType: data.messageType
    });
  };

  let sendMessage = function (message) {
    socket.emit('new message', {
      uid: uid,
      remoteUid: remoteUid,
      messageContent: message,
      userName: userName,
      playerAvatar: playerAvatar,
      userAvatar: userAvatar,
      messageType: NORMAL

    });
  }
  let play;
  export default {
    name: 'game',
    components: {vNav},
    data () {
      return {
        balance: null,    //  
        price: 0,     //  
        online_num: 0,    //  
        in_game: false,     //   
        username: null,     //  
        uid: null,    //  id
        avatar: null,     //  
        deviceid: null,  //  id
        token: null,
        match_id: null,   //  ???
        history: null,    //  
        show_his: false,    //   
      }
    },
    created(){
      this.loading.show();
      this.userName = this.cookie.get('username');
      this.uid = this.playerUid;
      this.balance = this.cookie.get('balance') || 0;
      this.deviceid = this.$route.query.id;
      this.token = this.cookie.get('token');
      this.uid = this.cookie.get('id');
      this.avatar = this.cookie.get('avatar');
      socket.emit('user joined', {
        username: this.userName,
        uid: this.uid,
        channel: this.deviceid
      });

    },
    mounted(){
      canvas = document.getElementById('canvas');
      this.ajax('enterDeviceRoom', `token=${this.token}&deviceid=${this.deviceid}`, this.init);
    },
    methods: {
      init(d){
        this.loading.hide();
        if (d.code !== 200) this.toast(d.descrp);
        else {
          this.price = d.data.price;
          this.online_num = d.data.online_num;
          let player = new Kinlink.Player(d.data.h5_play, {canvas: canvas});
        }
      },
      sendCaozuo(type){
        let data = {
          uid: this.uid,
          remoteUid: this.deviceid,
          playerUid: this.uid,
          playerUserName: this.userName,
          playerAvatar: this.avatar,
          matchID: this.match_id,
          messageType: type
        };
        let fund = {
          "13": this.apply_pay,
        };
        if (type === 11) this.in_game = false;
        sendCaozuo(data, fund[type])
      },
      play(){
//          
//        this.sendCaozuo(13);
        const _this = this;
        this.loading.show("");
        this.ajax('connect', `token=${this.token}&deviceid=${this.deviceid}`, function (d) {
          _this.loading.hide();
          if (d.status === 1) {
            _this.match_id = d.data.match_id;
            _this.ajax('get_info', `id=${_this.uid}&token=${_this.token}`, function (d) {
              if (d.code === 200) {
                _this.cookie.set('balance', d.data.balance);
                _this.balance = d.data.balance
              }
            });
            _this.sendCaozuo(13);
          }
          else if (d.status === 0) _this.toast(`${d.data.user_nicename}`);
          else if (d.status === 2) _this.toast(`${d.data.user_nicename}1`);
          else _this.toast(d.descrp)
        });
      },
      apply_pay(){
        this.in_game = true;
      },
      show_history(){
        if (this.history === null) {
          this.loading.show();
          const _this = this;


          this.ajax("history", `deviceid=${this.deviceid}`, function (d) {
            _this.loading.hide();
            if (d.code !== 200) _this.toast(d.descrp);
            else if (d.info.length > 0) {
              _this.history = d.info;
              _this.show_his = true;
            } else _this.toast("");
          });

        } else this.show_his = true;
      },

    },
  }



